import React from 'react'
import {useState} from 'react'
import { MDBContainer, MDBRow, MDBCol, MDBBtn } from 'mdbreact';
import * as bs from 'react-bootstrap'
import AppContext from './context'
import {useHistory} from 'react-router-dom'

function Search(props) {

    const context = React.useContext(AppContext)
    const history = useHistory()

    let categories = [
        'All',
        'Accidents & Emergencies',
        'Animals & Pets',
        'Babies, Kids & Family',
        'Business & Entrepreneurs',
        'Celebrations & Events',
        'Community & Neighbors',
        'Creative Arts, Music & Film',
        'Funerals & Memorials',
        'Travel & Adventure',
        'Medical, Illness & Healing',
        'Missions, Faith & Church',
        'Non-Profits & Charities',
        'Weddings & Honeymoons',
        'Other',
        'Sports, Teams & Clubs',
        'Education & Learning',
        'Volunteer & Service',
        'Competitions & Pageants',
        'Dreams, Hopes & Wishes',
    ]

    let goals = [
        'All',
        '<1000',
        '1000-5000',
        '5000-10000',
        '10000-50000',
        '50000-100000',
        '>100000',
    ]

    let percents = [
        'All',
        '<25%',
        '25-50%',
        '50-75%',
        '75-100%',
        '>100%',
    ]

    const [category, setCategory] = useState("All")
    const [goal, setGoal] = useState("All")
    const [percent, setPercent] = useState("All")

    function OnClick(){
        let categoryId = 0
        if (category == 'Accidents & Emergencies')
        {
            categoryId = 2
        }
        else if (category == 'Animals & Pets')
        {
            categoryId = 3
        }
        else if (category == 'Babies, Kids & Family')
        {
            categoryId = 4
        }
        else if (category == 'Business & Entrepreneurs')
        {
            categoryId = 5
        }
        else if (category == 'Celebrations & Events')
        {
            categoryId = 6
        }
        else if (category == 'Community & Neighbors')
        {
            categoryId = 7
        }
        else if (category == 'Creative Arts, Music & Film')
        {
            categoryId = 8
        }
        else if (category == 'Funerals & Memorials')
        {
            categoryId = 9
        }
        else if (category == 'Travel & Adventure')
        {
            categoryId = 10
        }
        else if (category == 'Medical, Illness & Healing')
        {
            categoryId = 11
        }
        else if (category == 'Missions, Faith & Church')
        {
            categoryId = 12
        }
        else if (category == 'Non-Profits & Charities')
        {
            categoryId = 13
        }
        else if (category == 'Weddings & Honeymoons')
        {
            categoryId = 14
        }
        else if (category == 'Other')
        {
            categoryId = 15
        }
        else if (category == 'Sports, Teams & Clubs')
        {
            categoryId = 16
        }
        else if (category == 'Education & Learning')
        {
            categoryId = 17
        }
        else if (category == 'Volunteer & Service')
        {
            categoryId = 18
        }
        else if (category == 'Competitions & Pageants')
        {
            categoryId = 19
        }
        else if (category == 'Dreams, Hopes & Wishes')
        {
            categoryId = 20
        }
        else 
        {
            categoryId = 0
        }

        let goalId = 0
        if (goal == 'All')
        {
            goalId = 0
        }
        else if (goal == '<1000')
        {
            goalId = 1
        }
        else if (goal == '1000-5000')
        {
            goalId = 2
        }
        else if (goal == '5000-10000')
        {
            goalId = 3
        }
        else if (goal == '10000-50000')
        {
            goalId = 4
        }
        else if (goal == '50000-100000')
        {
            goalId = 5   
        }
        else
        {
            goalId = 6
        }
        let isCharity = false
        if (document.getElementById('checkbox').checked)
        {
            isCharity = true
        }
        let hasBeneficiary = false
        if (document.getElementById('checkbox2').checked)
        {
            hasBeneficiary = true
        }
        let sort = false
        if (document.getElementById('checkbox3').checked)
        {
            sort = true
        }
        let percentId = 0
        if (percent == 'All')
        {
            percentId = 0
        }
        else if (percent == '<25%')
        {
            percentId = 1
        }
        else if (percent == '25-50%')
        {
            percentId = 2
        }
        else if (percent == '50-75%')
        {
            percentId = 3
        }
        else if (percent == '75-100%')
        {
            percentId = 4
        }
        else 
        {
            percentId = 5
        }
        var object = {
            category_id: categoryId,
            goal_id: goalId,
            is_charity: isCharity,
            has_beneficiary: hasBeneficiary,
            percent_raised: percentId,
            sort: sort,
        };

        context.updateResults(object)
        history.push("/search")
    }

    
    

        return (
                <div style={{textAlign: "center", fontFamily: "Times New Roman"}}>
                    <br/>
                    <p className="h4 text-center mb-4" style={{fontSize: "2rem"}}>Search</p>
                    <label htmlFor="category" className="grey-text">
                        Category
                    </label>      
                    <br/>
                    <select 
                    onChange={ e => setCategory(e.target.value) }
                    defaultValue={category}>
                    {Object.values(categories).map((item) => (
                        <option>{item}</option>
                    ))}        
                    </select>                   
                    <br/><br/>
                    <label htmlFor="goal" className="grey-text">
                        Goal Range
                    </label>      
                    <br/>
                    <select 
                    onChange={ e => setGoal(e.target.value) }
                    defaultValue={goal}>
                    {Object.values(goals).map((item) => (
                        <option>{item}</option>
                    ))}        
                    </select>                   
                    <br/><br/>
                    <label htmlFor="is_charity" className="grey-text">
                        GoFundMe campaign is a charity
                    </label>  
                    <br/>
                    <input type="checkbox"
                    name = "checkbox" 
                    id = "checkbox"
                    value="1"/>
                    <br/><br/>
                    <label htmlFor="is_charity" className="grey-text">
                        GoFundMe campaign has a beneficiary
                    </label>  
                    <br/>
                    <input type="checkbox"
                    name = "checkbox2" 
                    id = "checkbox2"
                    value="2"/>
                    <br/><br/>
                    <label htmlFor="percent" className="grey-text">
                        % of goal raised
                    </label>      
                    <br/>
                    <select 
                    onChange={ e => setPercent(e.target.value) }
                    defaultValue={percent}>
                    {Object.values(percents).map((item) => (
                        <option>{item}</option>
                    ))}        
                    </select>
                    <br/><br/>
                    <label htmlFor="is_charity" className="grey-text">
                        Sort by Quality Score
                    </label>  
                    <br/>
                    <input type="checkbox"
                    name = "checkbox3" 
                    id = "checkbox3"
                    value="3"/>
                    <br/>
                    <div className="text-center mt-4">
                     <bs.Button variant="outline-dark" onClick={OnClick}>Search</bs.Button>
                    </div>
                    <br/><br/>
                </div>
                     );

    }

export default Search