import React from 'react';
import './App.scss';
import * as bs from 'react-bootstrap'
import { BrowserRouter as Router, Switch, Route } from "react-router-dom"
import HeaderContainer from './header-container'
import FooterContainer from './footer-container'
import Home from './home'
import Search from './search'
import Output from './output'
import HomeRight from './home-right'
import Details from './details'

function App(props) {
  return (
    <Router>
      <bs.Container fluid className="p-0 min-vh-100 d-flex flex-column">
        <bs.Row noGutters className="flex-grow-0 flex-shrink-0 shadow-sm">
          <bs.Col className="px-3 py-2" style={{backgroundColor: "#9FBEDF"}}>
            <HeaderContainer/>
          </bs.Col>
        </bs.Row>
        <bs.Row noGutters className="flex-grow-1">
          <bs.Col md="3" style={{backgroundColor: "#f0f0f0"}}>
            <Switch>
              <Route path="/search">
                <Search/>
              </Route>
              <Route path="/details/:id">
                <Search/>
              </Route>
              <Route path="/">
                <Home/>
              </Route>
            </Switch>
          </bs.Col>
          <bs.Col md="9" style={{backgroundColor: "#f0f0f0"}}>
            <Switch>
              <Route path="/search">
                <Output/>
              </Route>
              <Route path="/details/:id">
                <Details/>
              </Route>
              <Route path="/">
                <HomeRight/>
              </Route>
            </Switch>
          </bs.Col>
        </bs.Row>
        <bs.Row noGutters className="flex-grow-0 flex-shrink-0">
          <bs.Col className="px-3 py-2" style={{backgroundColor: "#9FBEDF"}}>
            <FooterContainer/>
          </bs.Col>
        </bs.Row>
      </bs.Container>
    </Router>  
  );
}

export default App;
