import React from 'react'
import * as bs from 'react-bootstrap'
import AppContext from './context'
import {useHistory, useParams, Link} from 'react-router-dom'

function Details(props) {

    const {id} = useParams();
    const context = React.useContext(AppContext)
    const campaign = context.campaigns.find(c => {
        return (c.campaign_id === id)
    })

    let categoryId = ""

    if (campaign.category_id == 2)
    {
        categoryId = 'Accidents & Emergencies'
    }
    else if (campaign.category_id == 3)
    {
        categoryId = 'Animals & Pets'
    }
    else if (campaign.category_id == 4)
    {
        categoryId = 'Babies, Kids & Family'
    }
    else if (campaign.category_id == 5)
    {
        categoryId = 'Business & Entrepreneurs'
    }
    else if (campaign.category_id == 6)
    {
        categoryId = 'Celebrations & Events'
    }
    else if (campaign.category_id == 7)
    {
        categoryId = 'Community & Neighbors'
    }
    else if (campaign.category_id == 8)
    {
        categoryId = 'Creative Arts, Music & Film'
    }
    else if (campaign.category_id == 9)
    {
        categoryId = 'Funerals & Memorials'
    }
    else if (campaign.category_id == 10)
    {
        categoryId = 'Travel & Adventure'
    }
    else if (campaign.category_id == 11)
    {
        categoryId = 'Medical, Illness & Healing'
    }
    else if (campaign.category_id == 12)
    {
        categoryId = 'Missions, Faith & Church'
    }
    else if (campaign.category_id == 13)
    {
        categoryId = 'Non-Profits & Charities'
    }
    else if (campaign.category_id == 14)
    {
        categoryId = 'Weddings & Honeymoons'
    }
    else if (campaign.category_id == 15)
    {
        categoryId = 'Other'
    }
    else if (campaign.category_id == 16)
    {
        categoryId = 'Sports, Teams & Clubs'
    }
    else if (campaign.category_id == 17)
    {
        categoryId = 'Education & Learning'
    }
    else if (campaign.category_id == 18)
    {
        categoryId = 'Volunteer & Service'
    }
    else if (campaign.category_id == 19)
    {
        categoryId = 'Competitions & Pageants'
    }
    else if (campaign.category_id == 20)
    {
        categoryId = 'Dreams, Hopes & Wishes'
    }

    let currentAmount = parseInt(campaign.current_amount)
    let goal = parseInt(campaign.goal)

    let percent = currentAmount / goal

    let charity = 'No'
    if (campaign.is_charity)
    {
        charity = 'Yes'
    }
    let beneficiary = 'No'
    if (campaign.is_beneficiary)
    {
        beneficiary = 'Yes'
    }
    let barchart = 0
    if (parseInt(campaign.quality) < .5)
    {
        barchart = 5
    }
    else if (parseInt(campaign.quality) < 1.0)
    {
        barchart = 10
    }
    else if (parseInt(campaign.quality) < 1.5)
    {
        barchart = 15
    }
    else if (parseInt(campaign.quality) < 2.0)
    {
        barchart = 20
    }
    else if (parseInt(campaign.quality) < 2.5)
    {
        barchart = 25
    }
    else if (parseInt(campaign.quality) < 3.0)
    {
        barchart = 30
    }
    else if (parseInt(campaign.quality) < 3.5)
    {
        barchart = 35
    }
    else if (parseInt(campaign.quality) < 4.0)
    {
        barchart = 40
    }
    else 
    {
        barchart = 45
    }


    return (
        <div style={{fontFamily: "Times New Roman"}}>
            <br/>
            <h1>{campaign.title}</h1>
            <br/>
            <bs.Row>
                <bs.Col md="4">
                    <h5>Creator: {campaign.user_first_name} {campaign.user_last_name}</h5>
                </bs.Col>
                <bs.Col md="4">
                    <h5>Location: {campaign.location_city}, {campaign.location_country}</h5>
                </bs.Col>
                <bs.Col md="4">
                    <h5>Category: {categoryId}</h5>
                </bs.Col>
            </bs.Row>
            <br/>
            <p>{campaign.description}</p>
            <br/>
            <bs.Row>
                <bs.Col md="6">
                    <bs.Row>
                        <h3>Quality Score: {campaign.quality}</h3>
                    </bs.Row>
                    <br/>
                    <bs.Row>
                        <p>The Quality Score is intended to distinguish between higher and lower quality campaigns. Although measuring 
                            the quality is impossible to do with complete accuracy, the quality score is designed to estimate the genuinity and legitimacy of the campaign. The Quality Score is calculated based on a variety of metrics, including the category, the goal, the current amount,
                            the amount of donators, the description length, the amount of social media shares, whether or not the 
                            campaign has a beneficiary, whether or not the campaign is linked to the user's Facebook, whether or not the 
                            campaign is deactivated, and whether or not the campaign updates automatically post to Facebook. 
                        </p>
                    </bs.Row>
                    <br/>
                    <bs.Row>
                        <bs.Image src={`${process.env.PUBLIC_URL}/barcharts/${barchart}.png`} width="400rem" alt='barchart'/>
                    </bs.Row>
                </bs.Col>
                <bs.Col md="6">
                    <bs.Row>
                        <bs.Col md="4">
                        <b>Current Amount</b>
                        </bs.Col>
                        <bs.Col md="4">
                        <b>Goal</b>
                        </bs.Col>
                        <bs.Col md="4">
                        <b>% Raised</b>
                        </bs.Col>
                    </bs.Row>
                    <bs.Row>
                        <bs.Col md="4">
                        {campaign.current_amount}
                        </bs.Col>
                        <bs.Col md="4">
                        {campaign.goal}
                        </bs.Col>
                        <bs.Col md="4">
                        {(percent*100).toFixed(2)}%
                        </bs.Col>
                    </bs.Row>
                    <br/><br/>
                    <bs.Row>
                        <bs.Col md="6">
                        <b># of Donators</b>
                        </bs.Col>
                        <bs.Col md="6">
                        <b>Average Amount Donated</b>
                        </bs.Col>
                    </bs.Row>
                    <bs.Row>
                        <bs.Col md="6">
                        {campaign.donators}
                        </bs.Col>
                        <bs.Col md="6">
                        ${parseInt(campaign.average_amount).toFixed(2)}
                        </bs.Col>
                    </bs.Row>
                    <br/><br/>
                    <bs.Row>
                        <bs.Col md="4">
                        <b>Campaign Hearts</b>
                        </bs.Col>
                        <bs.Col md="4">
                        <b># Times Shared</b>
                        </bs.Col>
                        <bs.Col md="4">
                        <b>Days Active</b>
                        </bs.Col>
                    </bs.Row>
                    <bs.Row>
                        <bs.Col md="4">
                        {campaign.campaign_hearts}
                        </bs.Col>
                        <bs.Col md="4">
                        {campaign.social_share_total}
                        </bs.Col>
                        <bs.Col md="4">
                        {campaign.days_active}
                        </bs.Col>
                    </bs.Row>
                    <br/><br/>
                    <bs.Row>
                        <bs.Col md="6">
                        <b>Is Campaign a Charity?</b>
                        </bs.Col>
                        <bs.Col md="6">
                        <b>Does Campaign have a Beneficiary?</b>
                        </bs.Col>
                    </bs.Row>
                    <bs.Row>
                        <bs.Col md="6">
                        {charity}
                        </bs.Col>
                        <bs.Col md="6">
                        {beneficiary}
                        </bs.Col>
                    </bs.Row>
                </bs.Col>
            </bs.Row>
            <br/><br/><br/>
            <a href={`${campaign.url}`}>
                <div style={{textAlign: "center"}}>   
                    <bs.Button variant="dark" style={{display: "inline-block"}} >
                        See GoFundMe Page
                    </bs.Button>
                </div>
            </a>
            <br/><br/><br/>
        </div>
    )
}

export default Details