import React from 'react'
import * as bs from 'react-bootstrap'
import AppContext from './context'
import {useHistory} from 'react-router-dom'

function HeaderContainer(props) {


    const history = useHistory()

    function OnClick(){
        history.push('/search')
    }
    function home(){
        history.push('/')
    }

    return (
        <bs.Navbar expand="lg">
        <a href="/" onClick={home}>
            <bs.Image src={`${process.env.PUBLIC_URL}/android-chrome-192x192.png`} width='75rem' alt='logo'/>
        </a>
        <bs.Navbar.Toggle aria-controls="basic-navbar-nav" />
        <bs.Navbar.Collapse id="basic-navbar-nav">
            <bs.Nav className="mr-auto">
            <span>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp;</span>
            <bs.Button variant="outline-dark" onClick={OnClick}>Search Page</bs.Button>
            <span>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp;</span>
            <a href="https://covid19gofundme.herokuapp.com/index/"><bs.Button variant="outline-dark">Prediction Calculator</bs.Button></a>
            </bs.Nav>
        </bs.Navbar.Collapse>
        </bs.Navbar>
    )
}

export default HeaderContainer