import React from 'react'
import * as bs from 'react-bootstrap'
import AppContext from './context'
import {useHistory, Link} from 'react-router-dom'

function Output(props) {

    const context = React.useContext(AppContext)

    var results = []
    var searchField = "category_id"
    var searchVal = context.searchResults.category_id
    for (var i = 0 ; i < context.campaigns.length ; i++)
    {
        if (searchVal != 0) {
            if (context.campaigns[i][searchField] == searchVal) {
                results.push(context.campaigns[i]);
            }
        }
        else 
        {
            results.push(context.campaigns[i]);
        }
    }
    var results2 = []
    searchField = "goal"
    var searchVal1 = 0
    var searchVal2 = 0
    if (context.searchResults.goal_id == 0)
    {
        searchVal1 = 0
        searchVal2 = 0
    }
    else if (context.searchResults.goal_id == 1)
    {
        searchVal1 = 0
        searchVal2 = 1000
    }
    else if (context.searchResults.goal_id == 2)
    {
        searchVal1 = 1000
        searchVal2 = 5000
    }
    else if (context.searchResults.goal_id == 3)
    {
        searchVal1 = 5000
        searchVal2 = 10000
    }
    else if (context.searchResults.goal_id == 4)
    {
        searchVal1 = 10000
        searchVal2 = 50000
    }
    else if (context.searchResults.goal_id == 5)
    {
        searchVal1 = 50000
        searchVal2 = 100000
    }
    else 
    {
        searchVal1 = 100000
        searchVal2 = 0
    }
    for (var i = 0 ; i < results.length ; i++)
    {
        if (searchVal2 != 0)
        {
            if (results[i][searchField] >= searchVal1 && results[i][searchField] < searchVal2) {
                results2.push(results[i]);
            }
        }
        else if (searchVal1 != 0)
        {
            if (results[i][searchField] >= searchVal1) {
                results2.push(results[i]);
            }
        }
        else
        {
            results2.push(results[i]);
        }
    }
    var results3 = []
    var searchField = "is_charity"
    var searchVal = context.searchResults.is_charity
    for (var i = 0 ; i < results2.length ; i++)
    {
        if (searchVal) {
            if (results2[i][searchField] == "True") {
                results3.push(results2[i]);
            }
        }
        else 
        {
            if (results2[i][searchField] == "False") {
                results3.push(results2[i]);
            }
        }
    }
    var results4 = []
    var searchField = "has_beneficiary"
    var searchVal = context.searchResults.has_beneficiary
    for (var i = 0 ; i < results3.length ; i++)
    {
        if (searchVal) {
            if (results3[i][searchField] == "True") {
                results4.push(results2[i]);
            }
        }
        else 
        {
            if (results3[i][searchField] == "False") {
                results4.push(results2[i]);
            }
        }
    }
    var results25 = []
    var results50 = []
    var results75 = []
    var results100 = []
    var resultsover = []
    for (var i = 0 ; i < results4.length ; i++)
    {
        let currentAmount = parseInt(results4[i]['current_amount'])
        let goal = parseInt(results4[i]['goal'])

        var percent = currentAmount / goal

        results4[i]['percent'] = percent

        if (percent < .25)
        {
            results25.push(results4[i])
        }
        else if (percent >= .25 && percent < .50)
        {
            results50.push(results4[i])
        }
        else if (percent >= .50 && percent < .75)
        {
            results75.push(results4[i])
        }
        else if (percent >= .75 && percent < 1)
        {
            results100.push(results4[i])
        }
        else 
        {
            resultsover.push(results4[i])
        }
    }
    var results5 = []
    var searchVal = context.searchResults.percent_raised
    if (searchVal == 0)
    {
        results5 = results4
    }
    else if (searchVal == 1)
    {
        results5 = results25
    }
    else if (searchVal == 2)
    {
        results5 = results50
    }
    else if (searchVal == 3)
    {
        results5 = results75
    }
    else if (searchVal == 4)
    {
        results5 = results100
    }
    else 
    {
        results5 = resultsover
    }

    if (context.searchResults.sort)
    {
        results5 = results5.sort((a, b) => (a.quality > b.quality) ? -1 : 1)
    }

    return (
        <bs.Table striped bordered hover >
            <thead>
                <tr>
                <th>Quality Score</th>
                <th>Title</th>
                <th>Current Amount</th>
                <th>Goal</th>
                <th>% Raised</th>
                <th># of Donators</th>
                <th>Country</th>
                <th>Details</th>
                </tr>
            </thead>
            <tbody>
            {Object.values(results5).map((item) => (
                <tr>
                    <td>{item.quality}</td>
                    <td>{item.title}</td>
                    <td>{item.current_amount}</td>
                    <td>{item.goal}</td>
                    <td>{((item.percent)*100).toFixed(1)}%</td>
                    <td>{item.donators}</td>
                    <td>{item.location_country}</td>
                    <td>
                        <Link
                            className="btn btn-light" 
                            to={`/details/${item.campaign_id}`} 
                        >
                            Details
                        </Link>
                    </td>
                </tr> 
            ))}                
            </tbody>
        </bs.Table>
    )
}

export default Output